import Head from 'next/head';
import Link from 'next/link';
import ThemeToggleButton from './ThemeToggleButton';

const Header = () => (
    <header className="main-header">
        <nav>
            <Link href="/" className='title_full'><strong className='title_header'>Autumn</strong> Softwares</Link>
        </nav>
        <nav className="main-nav" style={{ display: 'flex', alignItems: 'center', gap: '20px' }}>
            <div>
                <Link href="/" style={{ marginRight: '25px' }}>Início</Link>
                <Link href="/sobre" style={{ marginRight: '25px' }}>Sobre</Link>
                <Link href="/portfolio"style={{ marginRight: '50px' }}>Portfólio</Link>
                <Link href="/solicitacao"style={{ marginRight: '20px' }}>Solicitar Projeto</Link>
            </div>
            <ThemeToggleButton />
        </nav>
    </header>
);

const Footer = () => (
    <footer style={{ padding: '20px', borderTop: '1px solid #ddd', marginTop: '40px', textAlign: 'center' }}>
        <p>© {new Date().getFullYear()} Autumn Softwares. Todos os direitos reservados.</p>
    </footer>
);

export default function Layout({ children }) {
    return (
        <>
            <Head>
                <title>Autumn Softwares</title>
                <meta name="description" content="Portfólio de projetos da Autumn Softwares" />
                <link rel="icon" href="/favicon.ico" />
            </Head>
            <Header />
            <main>{children}</main>
            <Footer />
        </>
    );
}